.about{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    column-gap: 10px;
    margin-top: 10px;
}

.about_item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: $white;
    padding: 30px;
}

.text_wrapper{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    column-gap: 1em;
}

@media (max-width:768px) {
    .about{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        column-gap: 0;
        margin-top: 0;
    }
    .about_img{
        display: none;
    }
}
