.news {
    background: $white;
    padding: 30px;
    margin-top: 10px;
}

@media (max-width:768px) {
    .news {
        padding: 20px;
    }
}

.news_wrapper {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    column-gap: 10px;
}

@media (max-width:765px) {
    .news_wrapper{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        row-gap: 20px;
    }
    
}

.news_img{
    width: 100%;
    height: auto;
}

.news_title {
    margin-top: 20px;
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
}

.news_text {
    margin-top: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
}