.popular_analizes {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.card{
    display: flex;
    flex-direction: column;
    padding: 30px;
    background-color: $white;
    row-gap: 20px;
    
}
.text_wrapper{
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.code {
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
}

.deadlines {
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
}

.price {
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
}

.popular_analizes_swiper {
    overflow: hidden;
  }