html,
body {
    width: 100%;
    height: 100%;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
    font-family: $Nunitob;
    font-style: normal;
    font-weight: 400;
    margin: 0;
    padding: 0;
}

h1,
h2 {
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 68px;
    line-height: 100%;
}

h3 {
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 100%;
    color: $dark_blue;
}

h5 {
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
    color: $dark_blue;
}



a {
    text-decoration: none;
    color: $dark_blue;
}

a:hover {
    color: $blue;
}

a:active {
    color: $red;
}

body {
    font-family: $Nunito;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
    color: $dark_blue;
    background: $fon;
}

.br {
    border-radius: 12px;
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 40px;
    border: none;
    border-radius: 40px;
    font-family: 'Nunito Sans';
    font-style: normal;
    font-size: 20px;
    font-weight: 400;
    line-height: 100%;
    cursor: pointer;
}

.btn_big{
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 10px;
}

.btn_blue {
    background: $blue;
    color: $white;
}

.btn_blue svg {
    fill: $white;
}

.btn_blue:hover {
    background: $white;
    color: $blue;
}

.btn_blue:hover svg {
    fill: $blue;
}

.btn_blue:active {
    background: $red;
    color: $white;
}

.btn_blue:active svg {
    fill: $white;
}

.btn_white {
    background: $white;
    color: $dark_blue;
}

.btn_white svg {
    fill: $dark_blue;
}

.btn_white:hover {
    background: $white;
    color: $blue;
}

.btn_white:hover svg {
    fill: $blue;
}

.btn_white:active {
    color: $red;
}

.btn_white:active svg {
    fill: $red;
}

.btn_red_border {
    color: $red;
    border: 2px solid $red;
}

.btn_red_border svg {
    fill: $red;
}

.btn_red_border:hover {
    color: $blue;
    border: 2px solid $blue;
}

.btn_red_border:hover svg {
    fill: $blue;
}

.btn_red_border:active {
    color: $white;
    border: 2px solid $red;
    background: $red;
}

.btn_red_border:active svg {
    fill: $white;
}

.btn_red {
    background: $red;
    color: $white;
    border: 2px solid $red;
}

.btn_red:hover {
    background: $white;
    color: $red;
    box-sizing: border-box;
}

.container {
    margin-right: auto;
    margin-left: auto;
    width: 100%;
}

.lines {
    border-width: 1px 1px 0px 1px;
    border-style: solid;
    border-color: #BABABA;
    border-radius: 12px 12px 0px 0px;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
}

@media (min-width: 360px) {
    .container {
        max-width: 356px;
    }
}

@media (min-width: 480px) {
    .container {
        max-width: 472px;
    }
}

@media (min-width: 576px) {
    .container {
        max-width: 568px;
    }
}

@media (min-width: 768px) {
    .container {
        max-width: 748px;
    }
}

@media (min-width: 992px) {
    .container {
        max-width: 972px;
    }
}

@media (min-width: 1024px) {
    .container {
        max-width: 1004px;
    }
}

@media (min-width: 1280px) {
    .container {
        max-width: 1200px;
    }
}

@media (min-width: 1400px) {
    .container {
        max-width: 1320px;
    }
}