.analizes {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto;
    margin-top: 10px;
    column-gap: 10px;
}

.category {
    position: relative;
    align-self: start;
}

.btn_category {
    display: none;
}

@media (max-width:991px) {
    .btn_category {
        display: block;
        width: 100%;
    }
}

.category_list {
    flex-direction: column;
    list-style: none;
    row-gap: 10px;
    background-color: $white;
    padding: 30px;
}



@media (min-width:991.09px) {
    .category_list {
        display: flex;
        border-radius: 12px;
    }
}

@media (max-width:991px) {

    .category_list {
        position: relative;
        z-index: 1;
        border-radius: 0 0 12px 12px;
        padding: 20px;
        margin-bottom: -20px;
        transform: translateY(-20px);
    }

    .btn_category {
        position: relative;
        z-index: 2;
    }

    .category_list {
        display: none;
        row-gap: 15px;

    }

    .category_limk {
        display: block;
        padding: 10px 10px 10px 0;
    }
}

.category_list.category_list_active {
    display: flex;
}

.category_limk {
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
}

.analizes_list {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.analizes_item {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
        "code code deadline price"
        "title title title to_cart"
    ;
    padding: 30px;
    background-color: $white;
}


.analizes_title {
    grid-area: title;
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
}

.analizes_code {
    grid-area: code;
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
}

.analizes_deadline {
    grid-area: deadline;
    margin-right: 20px;
    justify-self: end;
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
}

.analizes_price {
    grid-area: price;
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
}

.btn_to_cart {
    grid-area: to_cart;
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 100%;
    background-color: $white;
}

.analizes_button_wrapper {
    display: flex;
    justify-content: center;
    align-items: end;
}

@media (min-width:1024.99px) {
    .analizes_item {
        grid-template-columns: 1fr 1fr auto;
        grid-template-rows: auto 1fr;
        grid-template-areas:
            "code deadline price"
            "title title to_cart"
        ;
        padding: 20px;
        row-gap: 10px;
    }
}

@media (max-width:1024px) {
    .analizes_item {
        grid-template-columns: 1fr 1fr 178px;
        grid-template-rows: auto 1fr;
        grid-template-areas:
            "code deadline price"
            "title title to_cart"
        ;
        padding: 20px;
        row-gap: 10px;
    }

    .btn_to_cart {
        font-weight: 600;
        font-size: 16px;
    }

    .btn_category:hover {
        color: $blue;
        border: 2px solid $blue;
    }

}


@media (max-width:992px) {

    .analizes {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr auto;
        row-gap: 10px;
    }

    .analizes_item {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto auto auto auto;
        grid-template-areas:
            "title"
            "code"
            "deadline"
            "price"
            "to_cart"
        ;
        padding: 20px;
    }

    .analizes_title {
        margin-bottom: 10px;
    }

    .analizes_deadline {
        margin-right: 0;
        justify-self: start;
    }

    .btn_to_cart {
        width: 100%;
    }

}