.analizes_hero {
    padding: 30px;
}

.analizes_hero_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.analizes_hero_img {
    margin-bottom: -6px;
}

.analizes_hero_img svg {
    width: 424px;
    height: auto;
}

@media (max-width:768px) {
    .analizes_hero_img svg {
        width: 302px;
    }

    .analizes_hero {
        padding: 20px;
    }
}

.analizes_hero_search {
    width: 100%;
}

@media (max-width:1000px) {
    .analizes_hero_search {
        display: flex;
    }
}

.analizes_hero_search button {
    background: $red;
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 20px;
    padding-left: 30px;

}

.analizes_hero_search input[type=text]{
 padding-left: 30px;
}


.analizes_hero_search input[type=text]::placeholder {
    font-weight: 400;
    font-size: 20px;
    line-height: 100%;
    color: $grey;
}

@media (max-width:768px) {
    .analizes_hero_search input[type=text]{
        padding-left: 20px;
       }
}
@media (max-width:767px) {
    .analizes_hero_search button {
        padding: 10px;
        padding-left: 20px;
    }

    .analizes_hero_search input[type=text]::placeholder {
        font-size: 14px;
        line-height: 130%;
    }
}

.analizes_hero_text {
    font-weight: 400;
    font-size: 20px;
    line-height: 100%;
    margin-right: 40px;
}

@media (max-width:480px) {
    .analizes_hero_text {
       display: none;
    }
    .analizes_hero_search button{
        padding-left: 30px;
        padding-right: 30px;
    }
}
