.reviews_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px;
}

.reviews_header_text {
    margin-top: 10px;
    display: flex;
    align-items: center;
}

@media (max-width:991px) {
    .reviews_header_text{
        display: none;
    }
}

.yandex_description {
    padding-left: 10px;
    font-weight: 400;
    font-size: 12px;
}

.reviews_slider {
    overflow: hidden;
    margin-top: 10px;
}

.reviews_slide {
    padding: 30px;
    background: $white;
}

@media (max-width:991px) {
    .reviews_slide {
        padding: 20px;
    }
}

.slide_header {
    display: flex;
    justify-content: space-between;
}

.slider_header_item {
    display: flex;
    flex-direction: column;
}

.slider_text {
    margin-top: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
}

.reviews .swiper-slide {
    height: auto;
}