footer {
    position: relative;
    background: $dark_blue;
    color: $white;
    border-radius: 12px 12px 0px 0px;
    padding: 30px;
    margin-top: 10px;
    font-weight: 400;
    font-size: 14px;
}

footer a {
    color: $white;
    cursor: pointer;
}

footer ul {
    list-style: none;
}

.btn_menu_footer,
.btn_clouse_footer {
    display: none;
}

.footer_menu_wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;

    row-gap: 10px;
    justify-content: space-between;
    list-style: none;
}

.footer_menu {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.footer_menu svg {
    margin-bottom: 20px;
}

.copywrite {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.footer_logo {
    display: none;
}

@media (min-width:991px) {
    .footer_menu_inner, 
    .footer_menu_logo{
        display: none;
    }
    .footer_menu_logo{
        display: block;
    }
}

@media (max-width:991px) {
    footer{
        padding: 20px;
    }

    .footer_menu_logo{
        display: none;
    }

    .footer_menu_wrapper {
        display: none;
    }

    .btn_menu_footer {
        display: block;
    }

    .footer_logo {
        display: block;
    }

    .button_wrapper {
        display: flex;
        justify-content: space-between;
    }
    .btn_clouse_footer{
        display: block;
    }
}

@media (max-width:768px) {
    .footer_menu_logo{
        display: none;
    }
}

@media (max-width:766px) {
    .copywrite {
        
        flex-direction: column;
        
    }
}

@media (max-width:480px) {
    .button_wrapper{
        flex-direction: column;
        row-gap: 20px;
    }

    .footer_logo {
        align-self: center;
    }

    .copywrite{
        flex-direction: column;
        row-gap: 10px;
    }

    .footer_menu_logo{
        display: none;
    }


}

.footer_menu_wrapper.footer_menu_activ{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: $dark_blue;
    color: $white;
    padding: 20px;
    padding-top: 22px;
    border-radius: 12px 12px 0 0;

}

.footer_menu_activ a{
    color: $white;
   
}

.footer_menu_activ .footer_menu_inner{
    display: flex;
    justify-content: space-between;
}

.footer_menu_activ button{
    border: 0;
    background: $dark_blue;
}