.analiz {
    display: grid;
    grid-template-columns: auto 30%;
    grid-template-rows: 1fr;
    column-gap: 10px;
    margin-top: 10px;
}


.analiz_item>div {
    background: $white;
    padding: 30px;
    border-radius: 12px;
}

.analiz_title {
    margin-bottom: 20px;
}

.analiz_text {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    
}

.item_content {
    display: grid;
    row-gap: 10px;
    column-gap: 10px;
    grid-template-areas:
        "place deadline"
        "description description"
        "composition composition"
        "preparation preparation"
    ;
}

@media (max-width:991px) {
    .analiz {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
        column-gap: 10px;
        row-gap: 10px;
        margin-top: 10px;
        grid-template-areas:
        "item_price item_price"
        "item_content item_content"
    ;
    }

    .analiz_item>div {
        padding: 20px;
    }

    .item_content{
        grid-area: item_content;
        row-gap: 10px;
    }
    .item_price{
        grid-area: item_price;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        column-gap: 10px;
    }

    .item_content {
        display: grid;
        row-gap: 10px;
        column-gap: 10px;
        grid-template-areas:
            "place place"
            "deadline deadline"
            "description description"
            "composition composition"
            "preparation preparation"
        ;
    }
}

@media (max-width:767px) {
    .analiz_price_text{
        font-size: 50px;
    }
}
@media (max-width:575px) {
    .analiz_price_text{
        font-size: 30px;
    }
    .btn_analiz_price{
        font-size: 16px;
    }
}

@media (max-width:479px) {
    .item_price{
        grid-area: item_price;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr auto;
        column-gap: 10px;
    }
}


.analiz_place {
    grid-area: place;
}

.analiz_deadline {
    grid-area: deadline;
}

.analiz_description {
    grid-area: description;
}

.analiz_composition {
    grid-area: composition;
}

.analiz_preparation {
    grid-area: preparation;
}

.item_price {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.analiz_price {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.analiz_price_text {
    font-weight: 600;
    font-size: 68px;
    line-height: 100%;
}

.attention_text {
    font-family: 'Nunito Sans';
    color: $red;
    font-size: 14px;
    list-style: none;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;
}

.analiz_text {
    font-family: 'Nunito Sans';
    margin-top: 10px;
    list-style: none;
    font-size: 14px;
    list-style: none;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;
}