.hero {
    display: flex;
    flex-direction: column;
    background-color: $blue;
    padding: 30px;
    padding-bottom: 0;
}

.hero_title {
    color: $white;
    margin-bottom: 20px;
}

.hero_wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
}

.item_img {
    display: flex;
    align-items: start;
    justify-content: start;
}

.hero_img{
    width: 100%;
    height: auto;
}

@media (max-width:1024px) {
    .hero_wrapper{
        position: relative;
    }
    .btn_analizes{
        position: absolute;
        top: 0;
        left: 0;
    }

}

@media(max-width:992px) {

    .hero_title {
        font-size: 50px;
    }
}

@media (max-width:768px) {
    
    .hero {
        // padding: 30px;
    }

    .hero_title {
        font-size: 50px;
    }
}

@media (max-width:767px) {
    .hero_wrapper{
        position: initial ;
    }
    .btn_analizes{
        position: initial ;
    }
    .hero {
        padding: 20px;
    }

    .hero_wrapper{
        display: grid;
        grid-template-rows: 1fr auto;
        grid-template-columns: 1fr;
        grid-template-areas:   "img" "btn";
        justify-items: stretch;
        gap: 20px;
    }
    .item_analizes{
        grid-area: btn;
    }
    .item_img{
        grid-area: img;
    }
    .btn_analizes{
        display: block;
        width: 100%;
    }
}

@media (max-width:575px){
    .hero_title {
        font-size: 30px;
    }
    .hero_img{
        height: 178px;
    }
}