.actions_hero_item svg {
    margin-bottom: -6px;
}

@media (max-width:768px) {
    .hero.actions_hero{
        padding-bottom: 0;
    }
    .actions_hero_item svg {
        margin-bottom: -6px;
        width: 300px;
        height: auto;
    }
    .actions_text{
        padding: 20px;
    }
}
@media (max-width:480px) {
    .hero.actions_hero{
        padding-bottom: 0;
    }
    .actions_hero_item svg {
        margin-bottom: -6px;
        width: 272px;
        height: auto;
    }
}

