.contacts {
    margin-top: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    column-gap: 10px;
}

@media (max-width:991px) {
    .contacts {
        grid-template-columns: 1fr;
        grid-template-rows: auto 1fr;
        row-gap: 10px;
    }
}


.contacts_list,
.contacts_map {
    background: $white;
    padding: 30px;
}

.contacts_map svg {
    width: 425px;
    height: 258px;
}

@media (max-width:479px) {
    .contacts_map svg {
        width: 272px;
        height: 197px;
    }
}

.item_wrapper {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.contacts_item_inner {
    display: flex;
    align-items: center;
    column-gap: 10px;
}

.map_title {
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 100%;
    color: $dark_blue;
}

.contacts_item {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    margin-top: 20px;
}

@media (max-width:991px) {
    .contacts_item {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        row-gap: 10px;
    }

    .contacts_list,
    .contacts_map {
        background: $white;
        padding: 20px;
    }
}

@media (max-width:576px) {
    .contacts_item {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr auto;
    }

    .item_wrapper {
        row-gap: 10px;
    }
}

.map_non {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
}

.map_title {
    align-self: self-start;
}

.btn_map {
    width: 100%;
}

.map_show{
    display: none;
}

#mymap{
    height: 368px;
    
}

.map_show_active{
    display: block;
}

.map_non_unactive{
    display: none;

}