.header_top {
  background-color: $dark_blue;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $white;
  border-radius: 0px 0px 12px 12px;
  padding: 0 30px 0 30px;
  column-gap: 20px;
}

.header_search {
  display: flex;
  box-sizing: border-box;
  flex-grow: 1;
}

.header_search input[type=text] {
  flex-grow: 1;
  padding: 10px;
  border: none;
  float: left;
  background: $white;
  border-radius: 40px 0px 0px 40px;
}

.header_search input[type=text]::placeholder {
  color: $grey;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
}

.header_search button {
  flex-grow: 0;
  float: right;
  padding: 10px;
  background: $blue;
  border-radius: 0px 40px 40px 0px;
  padding: 10px 20px;
  color: white;
  border: none;
  cursor: pointer;
}

.header_search::after {
  content: "";
  clear: both;
  display: table;
}

.header_contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.contacts_address,
.contacts_phone {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
}

.contacts_address {
  margin-right: 20px;
}

.header_contacts_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 4px;
}

.header_contacts_item a {
  color: $white;
  text-decoration: none;
}

.header_contacts_item a:hover {
  color: $blue;
}

.btn_call {
  display: flex;
  column-gap: 10px;
  display: none;
}


@media (max-width:1200px) {
  .btn_call {
    display: none;
  }
}

@media (max-width: 1000px) {
  .header_search {
    display: none;
  }
}

@media (min-width: 1001px) {

  .header_search,
  .header_contacts {
    display: flex;
  }
}

@media(max-width:768px) {
  .header_contacts {
    display: none;
  }

  .btn_call {
    display: flex;

  }
}

@media(max-width:360px) {
  .btn_call {
    width: 100%;
  }
}

// menu
.header_menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 10px;
  margin: 10px 0;
}

.nav_list {
  display: flex;
  background: $white;
  padding: 0 20px;
  list-style-type: none;
  border-radius: 40px;
  justify-content: space-between;
  align-items: center;
  column-gap: 40px;
}

// @media (max-width: 991px) {
//   .nav_list {
//     display: none;
//   }
// }

.nav_clouse_wrapper {
  display: none;
}

.nav_item {
  background: $white;
  position: relative;
}

.nav_link {
  padding: 10px 10px;
  display: block;
  color: $dark_blue;

}

.nav_link:hover {
  color: $blue;
}

.dropdown_list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  display: none;
  background: $white;
  padding: 10px 30px 30px 30px;
  row-gap: 10px;
  border-radius: 0px 0px 12px 12px;
}

.nav_item:hover ul {
  position: absolute;
  top: 40px;
  left: 0;
  display: flex;
}

.dropdown_link {
  color: $dark_blue;
}

.dropdown_link:hover {
  color: $blue;
}

.btn_cart {
  font-size: 20px;
  cursor: pointer;
  width: 100%;
}

.btn_link {
  flex-grow: 1;
}

.btn_menu {
  display: none;
}

.btn_cart {
  position: relative;
  box-sizing: border-box;
}

.btn_cart svg {
  fill: $red;
}


.cart_count {
  position: absolute;
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid $white;
  background: $red;
  left: 0;
  bottom: 0;
  color: $white;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn_clouse {
  border: 0;
  background: $white;
  padding: 0;

}

@media (max-width:1400px) {
  .nav_list {
    column-gap: 10px;
  }
}

@media (max-width:1200px) {
  .btn_text {
    display: none;
  }

  .nav_list {
    column-gap: 10px;
  }

}



@media (max-width: 991px) {

  .header {
    position: relative;
  }

  .btn_menu {
    display: block;
  }

  .nav_list {
    display: none;
  }

  .nav_item:hover ul {
    position: static;
  }

  .nav_list_active {
    display: flex;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    flex-direction: column;
    align-items: start;
    border-radius: 0px 0px 12px 12px;
    border-width: 0px 2px 2px 2px;
    border-style: solid;
    border-color: $grey;
    padding-bottom: 20px;
    z-index: 3;
  }

  .nav_clouse_wrapper {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
  }

  .nav_list_active .nav_link {
    font-size: 16px;
    padding-left: 0;
    padding-bottom: 0;
  }

  .nav_list_active .dropdown_list {
    display: flex;
    padding-bottom: 20px;
    padding-left: 0;
    padding-bottom: 0;
  }

  .nav_list_active .dropdown_link {
    font-size: 14px;
  }

  .btn_link {
    flex-grow: 1;
  }

  .btn_cart {
    padding-left: 38px;
    padding-right: 38px;
  }

  .btn_menu {
    width: 50%;
  }

  .btn_text {
    display: block;
  }
}

@media (max-width:576px) {
  .btn_call_text {
    display: none;
  }
}

@media (max-width:575px) {
  .btn_cart {
    padding: 10px 30px;
  }
}

@media (max-width:352px) {

  .btn_menu_text,
  .btn_text {
    display: none;
  }
}